.AboutContent{
  align-items: flex-start;
}

.AboutLogo{
  height: 200px;
  pointer-events: none;
  align-self: center;
  padding-top: 50px;
}

.AboutTitle{
  font-size: 48px;
  margin: 50px 100px;
  color: #b79906;
  text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
             0px 8px 13px rgba(0,0,0,0.1),
             0px 18px 23px rgba(0,0,0,0.1);
}

.AboutText{
  font-size: 20px;
  color: white;
  margin: 0 150px;
}

.EndpointButton{
  z-index: 2;
  max-width: 270px;
  width: 100%;
  height: 60px;
  font-weight: 600;
  font-size: 36px;
  line-height: 36px;
  letter-spacing: 0.1em;
  position: absolute;
	left: 50%;
  down: 50px;
	color: #b79906;
  cursor: pointer;
  text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
             0px 8px 13px rgba(0,0,0,0.1),
             0px 18px 23px rgba(0,0,0,0.1);
}

